import React, {useEffect} from 'react';

const KakaoBtn=()=>{
    useEffect(()=>{
      if (window.Kakao) {
        const kakao = window.Kakao
        // 중복 initialization 방지
        if (!kakao.isInitialized()) {
          kakao.init(process.env.REACT_APP_KAKAO_JS_KEY)
        }
        kakao.Link.createDefaultButton({
          // Render 부분 id=kakao-link-btn 을 찾아 그부분에 렌더링을 합니다
          container: '#kakao-link-btn',
          objectType: 'feed',
          content: {
            title: '갯마을바지락칼국수보쌈',
            description: '#칼국수 #보쌈 #당수동 #맛집',
            imageUrl: 'images/icons/logo192.png', //추후 s3에서 가져오자
            link: {
              mobileWebUrl: 'https://map.naver.com/v5/entry/place/55872880?c=14131029.8661158,4479949.7440585,17,0,0,0,dh',
              webUrl: 'https://map.naver.com/v5/entry/place/55872880?c=14131029.8661158,4479949.7440585,17,0,0,0,dh',
            },
          },
          buttons: [
            {
              title: '웹으로 보기',
              link: {
                mobileWebUrl: 'https://map.naver.com/v5/entry/place/55872880?c=14131029.8661158,4479949.7440585,17,0,0,0,dh',
                webUrl: 'https://map.naver.com/v5/entry/place/55872880?c=14131029.8661158,4479949.7440585,17,0,0,0,dh',
              },
            },
            {
              title: '앱으로 보기',
              link: {
                mobileWebUrl:'https://map.naver.com/v5/entry/place/55872880?c=14131029.8661158,4479949.7440585,17,0,0,0,dh',
                webUrl: 'https://map.naver.com/v5/entry/place/55872880?c=14131029.8661158,4479949.7440585,17,0,0,0,dh',
              },
            },
          ],
        })
      }
    }, []);

    return(
      <div>
           <button id="kakao-link-btn">
              <img src="images/icons/kakaolink_btn_medium.png" alt="kakao-share-icon" />
          </button>
      </div>
    )
}

export default KakaoBtn;